const Noticia = (props) => {

    const {urlToImage, url, title, description, source} = props.noticia;

    let imagen;

    if(urlToImage !== ''){
        imagen = <img src={urlToImage} alt={title} className="card-img-top" />
    }else{
        image = <p>No imagen</p>
    }

    return ( 
        <div className="col-md6 col-12 mb-4">
            <div className="card">
                <div className="card-image">
                    {imagen}
                </div>
            </div>
            
        </div>
     );
}
 
export default Noticia;